@import '../../assets/css/variable.less';

.team-info {
  .team-bg-box {
    width: 100%;
    // background: linear-gradient(99.25deg, rgba(55, 86, 205, .2) 23.49%, rgba(205, 122, 55, .2) 83.55%);
    padding-bottom: 150px;
    background: url('./img/team-bg.jpg');
    background-size: 100% 100%;
    background-repeat: no-repeat;

    .team-title {
      text-align: center;
      padding-top: 63px;
      color: #FFFFFF;
      font-size: 48px;
      font-style: normal;
      font-weight: normal;
    }

    .team-describe {
      text-align: center;
      font-size: 20px;
      margin: 0 auto;
      margin-top: 60px;
      padding: 0 120px;
      font-weight: normal;

    }

    .hr {
      height: 75px;
      margin-top: 74px;
      position: relative;
      .down{
        position: absolute;
        width: 64px;
        height: 66px;
        text-align: center;
        line-height: 66px;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
      }
      .long{
        margin: 0 auto;
        width: 1180px;
        height: 1px;
        background: linear-gradient(to right,rgba(255,255,255,0.5),rgba(255,0,0,0));
      }
      .short{
        margin: 0 auto;
        width: 336px;
        height: 1px;
        margin-top: 50px;
        background: linear-gradient(to right,rgba(255,0,0,0),rgba(255,255,255,0.5));
      }
      .middle{
        margin: 0 auto;
        width: 666px;
        height: 1px;
        margin-top: 25px;
        background: linear-gradient(to right,rgba(255,255,255,0.4),rgba(255,0,0,0));
      }
    }

    .team-box-figure-info {
      width: 1334px;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;

      .figure-info {
        width: 632px;
        height: 540px;
        position: relative;
        display: flex;
        align-items: flex-end;
        margin-top: 80px;

        .figure-describe {
          width: 100%;
          height: 440px;
          background: linear-gradient(0deg, rgba(255, 255, 255, 0.03), rgba(255, 255, 255, 0.03)), linear-gradient(61.79deg, rgba(205, 122, 55, 0.07) 7.46%, rgba(130, 71, 229, 0.07) 69.49%);
          backdrop-filter: blur(6px);
          border: 1px solid;
          border-image: linear-gradient(to right, #8247E5,#CD7A37) 1;
          display: flex;
          align-items: center;
          justify-content: center;

          .figure-describe-box {
            .describe-box {
              text-align: center;
              padding: 0 22px;
              margin-top: 20px;
              color: #FFFFFF;
              font-style: normal;
              font-weight: normal;
              font-size: 16px;
            }

            img {
              margin-left: 39px;
              margin-top: 32px;
            }

            .name-job {
              text-align: center;
              margin-top: 24px;

              .name {
                font-size: 24px;
                color: #2564F4;
                font-weight: bold;
              }

              .job {
                font-size: 16px;
                font-weight: normal;
              }

            }
          }

        }

        .figure-logo {
          width: 200px;
          height: 200px;
          border-radius: 50%;
          position: absolute;
          left: 50%;
          top: 0;
          z-index: 1;
          transform: translateX(-50%);
        }

        .ceo {
          background: url('./img/ceo.svg');
          background-size: 100%;
        }

        .cto {
          background: url('./img/cto.svg');
          background-size: 100%;
        }

        .jason {
          background: url('./img/jason.svg');
          background-size: 100%;
        }

        .janice {
          background: url('./img/janice.svg');
          background-size: 100%;
        }

        .daniel {
          background: url('./img/daniel.svg');
          background-size: 100%;
        }
      }
    }
  }
}
@media only screen and (min-width : 1042px) and (max-width:1440px) {
  .team-info{
    .team-bg-box{
      .hr {
        height: 72px;
        .long{
          width: 864px;
        }
        .short{
          width: 259px;
        }
        .middle{
          width: 514px;
        }
      }
      .team-describe{
        padding: 0 60px;                            
      }
      .team-box-figure-info{
        width: 1013px;
        .figure-info {
          width: 488px;
          height: 548px;
          .figure-describe{
            height: 448px;
          }
        }
      }
    }
  }
}

@media only screen and (min-width : 660px) and (max-width:1042px) {
  .team-info{
    .team-bg-box{
      .hr {
        height: 72px;
        .long{
          width: 640px;
        }
        .short{
          width: 159px;
        }
        .middle{
          width: 314px; 
        }
      }
      .team-describe{ 
        padding: 0 60px;
      }
      .team-box-figure-info{
        width: 634px;
        display: block;
      }
    }
  }
}

@media only screen  and (max-width:659px) {
  .team-info{
    .team-bg-box{
      .hr {
        height: 72px;
        .long{
          width: 325px;
        }
        .short{
          width: 131px;
        }
        .middle{
          width: 259px;
        }
      }
      .team-title {
        min-width: 375px;
      } 
      .team-describe{
        padding: 0 24px;
        min-width: 375px;
        text-align: left;
      }
      .team-box-figure-info{
        width: 328px;
        display: block;
        .figure-info {
          width: 328px;
          height: 587px;
          .figure-logo {
            width: 135px;
            height: 135px;
          }
          .figure-describe{
            height: 520px;
          }
        }
      }
    }
  }
}

